import { ToyKit } from "meteor/meteortoys:toykit"
import { Mongol } from "../lib/index.js"

import "./_component";
import "./doc_controls";
import "./doc_editor";
import "./doc_insert";
import "./row_account";
import "./row_collection";
import "./row_header";
import "./row_trash";
import "./main.html"
import "./style/Mongol.css"

Meteor.startup(function() {
    Mongol.detectCollections();
    Mongol.hideMeteor();
    Mongol.hideVelocity();
    Mongol.hideMeteorToys();
});

Template.Mongol.helpers({
  Mongol_collections: function () {
    var config = ToyKit.get("Mongol");
    return config.collections || [];
  },
  active: function () {
    var active = ToyKit.get("Mongol_currentCollection");
    return active && "Mongol_expand";
  }
});

export { Mongol }